import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { from } from 'rxjs';
import { AboutmeComponent } from './aboutme/aboutme.component';
import { BlogComponent } from './blog/blog.component';
import { ContactComponent } from './contact/contact.component';
import { ContenidoPruebaComponent } from './contenido-prueba/contenido-prueba.component';
import { HomeComponent } from './home/home.component';
import { PortafolioComponent } from './portafolio/portafolio.component';
import { ResumeComponent } from './resume/resume.component';
import { DetalleProyectoComponent } from './detalle-proyecto/detalle-proyecto.component';
import { ExperienceComponent } from './experience/experience.component';

const routes: Routes = [
  {path: '', component: HomeComponent},
  {path: 'prueba', component: ContenidoPruebaComponent},
  {path: 'aboutMe', component: AboutmeComponent},
  {path: 'myBlog', component: BlogComponent},
  {path: 'contact', component: ContactComponent},
  {path: 'portafolio', component: PortafolioComponent},
  {path: 'resume', component: ResumeComponent},
  {path: 'detalle', component: DetalleProyectoComponent},
  {path: 'experience', component: ExperienceComponent},
  {path: '**', component: HomeComponent},
  
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
