import { Component, Directive, OnInit } from '@angular/core';
import { SeccionesService } from '../services/secciones.service';
import { ProyectosService } from '../services/proyectos.service';
import { Proyecto } from '../models/proyecto';
import { Router } from '@angular/router';

@Component({
  selector: 'app-portafolio',
  templateUrl: './portafolio.component.html',
  styleUrls: ['./portafolio.component.scss']
  // directives: [TituloPaginaComponent]
})
export class PortafolioComponent implements OnInit {

  index:number = 3;
  proyectosList:Array<Proyecto>;
  lenguajes:string = ""

  constructor(private proyectosInyectado:ProyectosService, private ruta:Router ) {    
    this.proyectosList = this.proyectosInyectado.proyectoLista;
  }  

  ngOnInit(): void {
  
  }

  detalleProyecto(proyecto:Proyecto){
    this.proyectosInyectado.proyectoDetalle = proyecto;
    this.ruta.navigateByUrl('detalle');
  }
}
